<template>
    <article>
        <p> </p>
        <p></p>

        <div class="buttonBase" v-on:click="toLogin" data-v-compReg></div>
    </article>
</template>

<script>
export default {
    
    methods:{
        toLogin(){
            this.$router.push({name:'Portal'})
        }
    }
}
</script>


<style>
    .buttonBase[data-v-compReg]{
        margin-top:30px;
    }
</style>



