<template>
    <section>
        <h1></h1>
        <p></p>

        <input type="text" name="mail" placeholder="" v-model="input.email" style="margin:10px 0px;">
        <input type="password" name="pw" placeholder="" v-model="input.updatedPW" style="margin:10px 0px;">
        <input type="password" name="pw" placeholder="" v-model="input.updatedPWRe" style="margin:10px 0px;">

        <div class="buttonBase" v-on:click="toSent"></div>
    </section>
</template>

<script>
export default {
    name : "ResetPassword",
    created: function(){
        // hide toggre
        this.$store.dispatch('setToggreState', false)

        this.input.key = this.$route.query.key
        
    },

    data(){
        return{
            input : {
                email:null,
                key  :null,
                updatedPW   : null,
                updatedPWRe : null
            }
        }
    },

    methods:{
        toSent(){
            // let url = location.hostname
            this.$store.dispatch('resetPassword', this.input)
        }
    }
}
</script>


