<template>
    <section>
        <h1></h1>
        <p></p>
        <p>std.ink.system@gmail.com </p>
        <p>std.ink.system@gmail.com </p>

        <input type="text" name="mail" placeholder="" v-model="email" style="margin:20px 0px;">

        <div class="buttonBase" v-on:click="toSent"></div>
    </section>
</template>

<script>
export default {
    name : "PasswordReminder",
    created: function(){
        // hide toggre
        this.$store.dispatch('setToggreState', false)
    },

    data(){
        return{
            email:null
        }
    },

    methods:{
        toSent(){
            // URL
            let url = process.env.RootUrl + "#/resetpassword?key="
            this.$store.dispatch('passwordReminder', {email:this.email, url:url})
        }
    }
}
</script>


