<template>
    <article>
        <table data-v-pwUpdate>
            <tr>
                <td></td>
                <td><input type="password" name="pw" placeholder=" " v-model="currentPW" data-v-pwUpdate></td>
            </tr>
            <tr>
                <td></td>
                <td><input type="password" name="pw" placeholder=" " v-model="updatedPW" data-v-pwUpdate></td>
            </tr>
            <tr>
                <td>()</td>
                <td><input type="password" name="pw-re" placeholder=" " v-model="updatedPWRe" v-on:keyup.enter="toRegister" data-v-pwUpdate></td>
            </tr>
        </table>
        <div class="info" data-v-pwUpdate>
            <p> </p>
        </div>

        <div class="buttonBase" v-on:click="toRegister"></div>

        <Modal
            v-show="showModal"
            v-bind:mtypeIn='2'
            v-on:modal-ok-event    = "modalOk"
        >
            <p slot="header">@TJC message</p>
            <template slot="body">
                <p v-html="modalMsg" />
            </template>
        </Modal>

    </article>
</template>

<script>
import Modal from '@/widgets/Modal'

export default {
    name: "UpdatePassWord",

    components :{
        Modal
    },

    data(){
        return{
            currentPW   : "",
            updatedPW   : "",
            updatedPWRe : "",

            modalMsg : "",

            showModal : false
        }
    },

    methods:{
        //  
        toRegister(){
            // 
            if(this.currentPW == "" || this.updatedPW == "" || this.updatedPWRe == "")
            {
                this.currentPW   = ""
                this.updatedPW   = ""
                this.updatedPWRe = ""

                this.modalMsg   = "()"
                this.showModal  = true
                return
            }

            // 
            else if(this.updatedPW != this.updatedPWRe)
            {
                this.currentPW   = ""
                this.updatedPW   = ""
                this.updatedPWRe = ""

                this.modalMsg   = "()"
                this.showModal  = true
                return
            }

            // 
            else if(this.currentPW == this.updatedPW)
            {
                this.currentPW   = ""
                this.updatedPW   = ""
                this.updatedPWRe = ""

                this.modalMsg   = ""
                this.showModal  = true
                return
            }

            // 
            this.$store.dispatch('updatePW', {currentPW:this.currentPW, updatedPW:this.updatedPW, updatedPWRe:this.updatedPWRe})
        },

        modalOk(){
            this.showModal = false
        }
    }
}
</script>

<style scoped>
    table[data-v-pwUpdate]{
        width: 100%;
        margin: 0 auto;
        /* margin-bottom: 30px; */
    }
    .info[data-v-pwUpdate]{
        margin: 30px 0px;
        font-size: 80%;
    }
</style>



